@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";

section {
  padding-top: 200px;
}

.experience-grid {
  padding-top: 10px;
}
.experience-card {
  background-color: rgba($color: $color-background-3, $alpha: 0.5);
  border-radius: 5px;
}
.experience-date {
  color: $tertiary-color;
  font-size: 1.3rem !important;
  font-family: $font-Open-Sans;
}
.experience-degree {
  color: $primary-color;
  font-family: $font-Open-Sans;
  font-size: 1.7rem !important;
  font-weight: 600 !important;
}
.experience-accomplishment {
  color: $tertiary-color;
  font-family: $font-Open-Sans;
  font-size: 1.5rem !important;
  padding-left: 1.5em;
  text-indent: -1.3em;
}

@media screen and (max-width: 599px) {
  .experience-accomplishment {
    color: $tertiary-color;
    font-family: $font-Open-Sans;
    font-size: 1.7rem !important;
    padding-left: 1.5em;
    text-indent: -1.2em;
  }
}

.experience-company {
  color: $primary-color;
  font-size: 1.3rem !important;
  font-family: $font-Open-Sans;
}
