@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";


.education-grid {
  padding-top: 25px;
  padding-bottom: 50px;
}
.education-card {
  background-color: rgba($color: $color-background-3, $alpha: 0.5);
  border-radius: 5px;
}
.education-year {
  color: $tertiary-color;
  font-size: 1.2rem !important;
  font-family: $font-Open-Sans;
}
.education-degree {
  color: $primary-color;

  font-family: $font-Open-Sans;
  font-size: 1.7rem !important;
  font-weight: 600 !important;
}
.education-accomplishment {
  color: $tertiary-color;
  font-family: $font-Open-Sans;
  font-size: 1.5rem !important;
  padding-left: 1.5em;
  text-indent: -1.3em;
}

@media screen and (max-width: 599px) {
  .education-accomplishment {
    color: $tertiary-color;
    font-family: $font-Open-Sans;
    font-size: 1.7rem !important;
    padding-left: 1.5em;
    text-indent: -1.2em;
  }
}

.education-school {
  color: $primary-color;
  font-size: 1.2rem !important;
  font-family: $font-Open-Sans;
}
