@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";

.MuiGridListTile-root {
  // background-color: rgba(165, 42, 42, 0.221);
  padding: 10px !important;
}

.project-image {
  object-fit: cover !important;
  width: 110%;
  height: 100%;
  // left: 10px !important;
  // background-color: #000000;
  // opacity: .6;
}

.image-box {
  box-shadow: inset 0 0 0 100vw rgba(0, 0, 0, 0.75);

  color: white;
  display: flex;
  align-items: center;
  justify-content: center;

  transition: box-shadow 0.3s ease-out;

  object-fit: cover !important;
  width: 100%;
  height: 100%;

  object-fit: contain !important;

  background-size: 100% !important;
  // background-repeat: no-repeat;
}
.image-box:hover {
  box-shadow: inset 0 0 0 100vw rgba(53, 14, 0, 0.9);
}

.wrap {
  width: 100%;
  padding-top: 125px;
  padding-bottom: 125px;

  text-indent: 0px;
}
.noshow,
.wrap:hover .show {
  display: none;
  // text-align: center;
}
.wrap:hover .noshow {
  display: block;
}

.project-link {
  color: $color-secondary-main !important;
  // margin-top: 3vh !important;
  display: flex;
  justify-content: space-evenly;
  // align-self: center;
}
