@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";

.skills-card-container {
  padding-top: 100px;
}
.skills-text {
  font-family: $font-Open-Sans;
  font-size: 1.9rem;
  color: $color-light-blue;
  font-weight: 100;
  line-height: 1.5em;
}
.skill-image {
  font-size: 5em;
}
.skill-icon {
  font-size: 5.5rem !important;
  :hover {
    color: $primary-color;
  }
}
.skills-grid {
  text-align: center;
  font-size: 1.4em;
}
.skills-name {
  color: $primary-color;
  font-family: $font-Open-Sans;
  font-size: 1.4rem !important;
}

.sub-heading {
  font-size: 2.7rem !important;
  line-height: 0.1rem;
  font-weight: 500 !important;
  padding-top: 1.5rem;

  padding-bottom: 0.1rem;
  font-family: $font-dosis;
  color: $tertiary-color;

  p {
    font-family: $font-dosis;
    text-align: center;
    font-size: 1.4em;
  }
  FontAwesomeIcon {
    font-size: 50em;
    align-items: center;
    height: 10rem !important;
    width: 10em;
    .svg-inline--fa {
      display: inline-block;
      font-size: inherit;
      height: 10em !important;
      overflow: visible;
      vertical-align: -0.125em;
    }
  }
}
FontAwesomeIcon {
  font-size: 50em;
  align-items: center;
  height: 10rem !important;
  width: 10em;
}

@media screen and (max-width: 599px) {
  .skills-text {
    font-family: $font-Open-Sans;
    font-size: 2rem;
    color: $color-light-blue;
    font-weight: 100;
    line-height: 1.3em;
  }
}
