@import "../../../Assets/scss/Typography.scss";
@import "../../../Assets/scss/Colors.scss";

@media screen and (max-width: 599px) {
  .cover-height {
    min-height: 100vh;
  }
  .cover-heading {
    font-size: 3em;
    text-align: left;
  }
  .cover-name {
    font-family: $font-dosis;
    font-size: 5.5em !important;
    padding-top: 0.5em;
    font-weight: 600;
  }
  .cover-image-mobile {
    display: inline-block;
    text-align: center;
    padding-top: 3rem;
    img {
      margin-left: auto;
      margin-right: auto;
      width: 90%;
    }
  }

  .cover-image {
    display: none;
  }
  .cover-text {
    display: none;
  }
  .cover-title {
    font-family: $font-dosis;
    font-size: 2.5rem !important;
    color: $color-blue;
    line-height: 3rem !important;
  }

  .tagline-text {
    padding: 0.5em;
  }
}
