@import "../scss/colors.scss";
@import "../scss/Typography.scss";
@import "../scss/buttons.scss";

$grid__bp-md: 1200;
html,
body {
  color: #fff;
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: $color-background-1;
  font-size: 65%;
}

.container {
  max-width: 1200px;
  margin: auto;
  padding: 0em 0.8em;
}
.padding-50{
  padding-top: 50px;
  padding-bottom: 50px;
}
.upright-text {
  writing-mode: vertical-rl;
  transform: rotate(180deg);
  text-align: right;
}
@media screen and (max-width: 599px) {
  .upright-text {
    display: none;
    padding-left: 260px;
    writing-mode: none;
    transform: rotate(270deg);
    text-align: center;
  }
}

.primary-color {
  color: $primary-color;
}
.secondary-color {
  color: $color-blue;
}
.vertical-center {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.vertical-bottom {
  position: absolute;
  bottom: 0;
}

.blockquote-footer {
  font-family: $font-Open-Sans;
  font-size: 2em;
}

@media screen and (max-width: 599px) {
  .site-wrapper {
    margin: 1em;
  }
  .blockquote-footer {
    font-family: $font-Open-Sans;
    font-size: 1.3em;
  }

  p {
    font-family: $font-Open-Sans;
    font-size: 1.3em;
    line-height: 1.25em;
  }
}

.section-height {
  min-height: 100vh;
}

.text-left {
  text-align: left;
}
