//   font-family: $font-dosis;
//   font-size: 2.5rem;
//   font-weight: 100;
//   line-height: 3rem;
//   padding: 1em oem;
// }
@media screen and (max-width: 599px) {
  .tag-text {
    font-family: $font-dosis;
    font-size: em;
    font-weight: 100;
  }
}

p {
  font-family: $font-Open-Sans;
  display: block;
  margin-block-start: 0.7em;
  margin-block-end: 0.7em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-size: 1.3em;
}

h2 {
  display: block;
  font-size: 1.5em;
  margin-block-start: 0.63em;
  margin-block-end: 0.63em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}
.mobile-only {
  display: none;
}
@media screen and (max-width: 599px) {
  .mobile-only {
    display: inline;
  }
}

.heading {
  font-size: 3.3rem;
  font-weight: 700 !important;
  font-family: $font-dosis;
  padding-bottom: 0.1rem;
  color: $primary-color;
}
.heading::before {
  content: "\2014 \00A0";
}

@media screen and (max-width: 599px) {
  .cover-heading {
    line-height: 0em;
  }
}
